___CSS_LOADER_EXPORT___.push([module.id, `/* STYLING TASK DISPLAY */
.taskContainer {
  width: 70%;
  min-height: 100px;
  /* background-color: #d4d2ce; */
  /* background-color: #e7e5e2; */
  border: rgba(139, 139, 139, 0.5) solid 1px;
  margin-top: 20px;
  padding: 10px 0;
  border-radius: 8px;

  display: flex;
  justify-content: center;
}

.taskList {
  width: 80%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0;
}

.listTopRow {
  display: grid;
  grid-template-columns: 20px 1fr 30px 30px;
  align-items: center;
  /* justify-items: center; */
  gap: 15px;

  font-size: 1.1rem;
  font-weight: 600;

  position: relative;
}

.flagSelection {
  appearance: none;
  width: 30px;
  height: 30px;
  border: grey solid 1px;
  border-radius: 2px;
  cursor: pointer;
  font-size: 1.1rem;

  text-align: center;
  outline: none;
}

.taskRemoveButton {
  font-size: 1.2rem;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 30px;
  height: 30px;

  background-color: transparent;
  border: grey solid 1px;
  border-radius: 2px;
}

.checkbox {
  width: 20px;
  height: 20px;
  margin: 0;
}

li {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.taskDescription {
  margin: 0 0 0 35px;
}

/* ////////////////////////////////////////// */
/* List Third Row */
.listThirdRow {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 2px;

  margin: 0 0 0 30px;
}

/* due date */
.dueDateDisplay {
  /* color: rgb(36, 109, 255); */
  margin: 0;
  padding: 0;
}

/* Calendar Button */
.calendarButton {
  width: 25px;
  /* height: 25px; */

  color: transparent; /* set text transparent */
  overflow: hidden;
  background-color: transparent;
  border: transparent;
  border-radius: 2px;
}

.calendarButton::-webkit-calendar-picker-indicator {
  background-color: transparent;
  cursor: pointer;
  opacity: 0.8;
  font-size: 1.3rem;
  padding: 0;
  /* border: red solid 2px; */
}

/* //////////////////////////////////////////// */

.divider {
  width: 100%;
  height: 1px;
  background-color: rgba(139, 139, 139, 0.5);
}

/* Hide the last divider */
.divider:last-child {
  display: none;
}

/* Strike thru completed tasks */
.completed .taskName,
.completed .taskDescription {
  text-decoration: line-through;
}
