___CSS_LOADER_EXPORT___.push([module.id, `/* GENRAL STYLING - MAIN LAYOUT */

:root {
  --main-bg-color: #fffbe8;
}

body {
  background-color: var(--main-bg-color);
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;

  margin: 0;
  padding: 0;
  height: 100%;
}

.todo-app {
  display: grid;
  grid-template-columns: 1fr 4fr;
  min-height: 100vh;
}

nav {
  display: flex;
  flex-direction: column;
  gap: 100px;
}

.mainNav {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.mainSection {
  padding: 5%;
}

.todoPage {
  display: block;
}

.todayPage {
  display: none;
}

.projectPage {
  display: none;
}

.addTaskButton {
  padding: 5px 10px;
  font-size: 1rem;
}

button:hover {
  cursor: pointer;
}

@media (width<500px) {
  .todo-app {
    display: flex;
    flex-direction: column;

    /* display: grid;
    grid-template-rows: 1fr 2fr; */
  }
  .sidebar {
    height: 20vh;
    min-height: 20px;
    /* background-color: blue; */

    /* display: flex;
    flex-direction: row; */
  }

  nav {
    display: flex;
    flex-direction: row;
  }
}
