___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  --dialog-bg-color: rgb(255, 255, 255);
  --high-priority-color: #e77b50;
  --med-priority-color: #fdc04e;
  --low-priority-color: #6dbbda;
  --no-priority-color: var(--main-bg-color);
}

/* STYLING TASK INPUT FORM */

dialog {
  background-color: var(--dialog-bg-color, white);
  border: none;
  /* border-radius: 20px; */
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;

  border: rgb(191, 191, 191) solid 1px;
  background-color: hwb(0 100% 0%);
  padding: 20px;
  border-radius: 10px;
}

input[type="text"] {
  border: none;
  background-color: transparent;
  /* height: 20px; */
}

.entryName {
  font-size: 1.2rem;
  font-weight: 600;
}

.entryDescription {
  font-size: 1.1rem;
}

input[type="text"]:focus {
  outline: none;
}

/* Prevent defaut auto fill white background */
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px var(--dialog-bg-color) inset !important;
  color: inherit !important;
}

input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px var(--dialog-bg-color) inset !important;
  color: inherit !important;
}

.entryName::placeholder {
  font-weight: 600;
}

.taskUtils {
  display: flex;
  justify-content: start;
  gap: 15px;
}

#dueDate {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  width: 120px;
}

#dueDate::-webkit-calendar-picker-indicator {
  /* background-color: red; */
  cursor: pointer;
  opacity: 0.8;
  font-size: 1.2rem;
}

/* Priority Style */
.priorityDiv {
  border: grey solid 1px;
  border-radius: 2px;

  display: flex;
  align-items: center;
  gap: 5px;

  padding: 4px 5px;
}

#priorityFlag {
  font-weight: 500;
  font-size: 1rem;
  padding: 0;
  margin: 0;
}

#priorityChoice {
  background-color: "var(--dialog-bg-color, white)";
  /* background-color: #f18b63; */
  outline: none;

  /* appearance: none; */
  /* width: 80px; */
  /* padding: 0; */
  /* margin: 0; */

  cursor: pointer;
}

.noPriority {
  /* background-color: var(--dialog-bg-color, white); */
  background-color: var(--no-priority-color);
}

.highPriority {
  background-color: var(--high-priority-color);
}

.medPriority {
  background: var(--med-priority-color);
}

.lowPriority {
  background-color: var(--low-priority-color);
}

select {
  font-size: 1rem;
  border: none;
  /* background-color: rgb(253, 192, 81); */
}

/* Bottom 2 buttons */
.addOrCancel {
  display: flex;
  justify-content: end;
  gap: 10px;
}

form button {
  padding: 5px 10px;
  font-size: 1rem;
}
